import axios from 'axios';
import styles from 'css/book/detail.module.scss';
import { useEffect } from 'react';
import { useState } from 'react';
import SearchBar from './component/SearchBar';

import { useParams } from "react-router-dom";
import moment from 'moment';
function Detail() {
    let [data, setData] = useState([]);
    let { id } = useParams();


    function change_date(published_at) {
        const publish_date = moment(published_at).format('YYYY MM DD')
        return publish_date
    }


    useEffect(() => {
        console.log("");
        axios.get("/api/book/detail/" + id).then((response) => {
            let arr = [];
            arr.push(response.data);
            setData(arr);
        });
    }, [])

    useEffect(() => {
        console.log(data);
    }, [data])
    return (
        <div className={styles.wrap}>
            <SearchBar></SearchBar>
            {data.length>0 ?
                <div className={styles.topWrap}>
                    <div className={styles.imageBox}>
                        <img src={data[0].image}></img>
                    </div>
                    <div className={styles.infoBox}>
                        <h4>{data[0].title}</h4>
                        <ul>
                            <li>
                                <dt></dt>
                                <dl>{data[0].author}</dl>
                            </li>
                            <li>
                                <dt></dt>
                                <dl>{data[0].publisher}</dl>
                            </li>
                            <li>
                                <dt></dt>
                                <dl>{data[0].lastBooks}</dl>
                            </li>
                            <li>
                                <dt></dt>
                                <dl>{data[0].completionStatus}</dl>
                            </li>
                            <li>
                                <dt> </dt>
                                <dl>{data[0].createDateTime}</dl>
                            </li>
                        </ul>
                        <hr></hr>
                        <h5></h5>
                        <p>{data ? data[0].description : null}</p>
                        <hr></hr>
                        <h5> </h5>
                         
                    </div>
                </div> 
                : <> </>}
            <hr></hr>
            <div className={styles.maps}>
                <h4> </h4><br></br>
                <img src="/img/cartoon/map.PNG"></img>
            </div>
            <div className={styles.goback}>
                <a></a>
            </div>
        </div>

    )
}
export default Detail;