import axios from "axios"


export const getBookListPageing = async (params) => {
    let url = 'http://localhost:8080/book/list';
    try {
        let res = await axios.get(url,{params})
        return res.status == 200 ? res.data : "error";
    }
    catch (error) {
        return error
    }
}

export const getBookList = async () => {
    let url = 'http://localhost:8080/bookList';
    try {
        let res = await axios.get(url)
        return res.status == 200 ? res.data : "error";
    }
    catch (error) {
        return error
    }
}

export const save = async (params) => {
    try {
        let response = await axios.post("http://localhost:8080/bookAdd", null, {params})
        alert(" .");
    } catch {
        alert("");
    }
}

