import axios from "axios";
import { useEffect, useRef, useState } from "react";


import * as SockJS from 'sockjs-client';
import * as StompJs from '@stomp/stompjs';
const ROOM_SEQ = 1;

const Chat = () => {
    const client = useRef({});
    const [chatMessages, setChatMessages] = useState([]);
    const [message, setMessage] = useState("");

    useEffect(()=>{
        connect();
        return () => disconnect();
    },[])

    const connect = () => {
        client.current = new StompJs.Client({
          // brokerURL: "ws://localhost:8080/ws/websocket", //    
          webSocketFactory: () => new SockJS("/ws"), // proxy  
          connectHeaders: {
            // "auth-token": "spring-chat-auth-token",
            login: 'user',
            password: 'password'
          },
          debug: function (str) {
            console.log(str);
          },
          reconnectDelay: 5000,
          heartbeatIncoming: 4000,
          heartbeatOutgoing: 4000,
          onConnect: () => { //      
            console.log('success');
            subscribe(); 
          },
          onStompError: (frame) => {
            console.error(frame);
          },
        });
    
        client.current.activate(); //  
      };

      const disconnect = () => { //  
        client.current.deactivate();
      };

      const subscribe = () => {
        client.current.subscribe("/topic/test", (body) => {
          console.log("test",body);
          setChatMessages((_chatMessages) => [..._chatMessages, JSON.parse(body)]);
        },{id:"user"});
      };

      const publish = (message) => {
        if (!client.current.connected) {
          return;
        }
    
        client.current.publish({destination: "/app/hello2",body: JSON.stringify({ roomSeq: ROOM_SEQ, message }),
        });
    
        setMessage("");
      };
    return (
        <div>
        {chatMessages && chatMessages.length > 0 && (
          <ul>
            {chatMessages.map((_chatMessage, index) => (
              <li key={index}>{_chatMessage.message}</li>
            ))}
          </ul>
        )}
        <div>
          <input
            type={"text"}
            placeholder={"message"}
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            onKeyPress={(e) => e.which === 13 && publish(message)}
          />
          <button onClick={() => publish(message)}>send</button>
        </div>
      </div>
    )
}
export default Chat;