import { useEffect } from "react";
import { useParams } from "react-router-dom";

function OpenerDetour(){
    let { id } = useParams();
    const openAelertTest=()=>{
    }
    function openAlert(){
        window.opener.alert("");
    }
    function open(){
        let winObj =window.open('http://localhost:8080/cancel',"", "width=800, height=700, toolbar=no, menubar=no, scrollbars=no, resizable=yes" );
        window.asdf=(a)=>{
            alert(a);
        };
    }
    function openerCodes(){
        window.opener.callbackFunction(id);
    }

    useEffect(()=>{
        openerCodes();
    },[])
    return(
        <div>
            .
            <button onClick={openAlert}></button>
            <button onClick={openAelertTest}></button>
            <button onClick={open}></button>
            <button onClick={openerCodes}></button>
        </div>
    )
}
export default OpenerDetour;