import styles from 'css/order/menuSelect.module.css';
import { FaPlus, FaMinus } from "react-icons/fa";
import { useState } from 'react';
import * as gvar from 'global_variables'
import axios from 'axios';
function MenuSelect({visible, closeModal, data}){
    let imgurl = gvar.BACK_BASE_URL+'/images/';
    
    let [quantity,setQuantity] = useState(1);    
    const plusQuantity = ()=>{
        console.log(quantity);
        if(quantity < 9){
            setQuantity(quantity+1);
        }
        else{
            alert("   .");
        }        
    }

    const minusQuantity = ()=>{
        console.log(quantity);
        if(quantity > 1){
            setQuantity(quantity-1);
        }
    }



    const addCart=()=>{
        let url = '/menu/cart';
        console.log("");
        axios.post(url,null,{
            params:{
                itemId : data.id,
                quantity : quantity
            }
        }
        )
        .then((result)=>{
            console.log(result);
        })
        .catch((e)=>{
            alert("error")
        });
        closeModal();
    }
    if(visible){
        return(
            <>            
                <div className={styles.wrap} >
                    <div className={styles.xbtn} onClick={closeModal}>
                     <FaPlus></FaPlus>
                    </div>          
                    <div className={styles.topBox}>
                        <img src={imgurl+data.photoPath} alt="menu"></img>
                    </div>
                    <div className={styles.middleBox}>
                        <h4></h4>
                        <div className={styles.countBox}>
                            <button className={styles.minusBtn} onClick={minusQuantity}>
                                <FaMinus></FaMinus>
                            </button>
                            <p>{quantity}</p>
                            <button className={styles.plusBtn} onClick={plusQuantity}>
                                <FaPlus></FaPlus>
                            </button>
                        </div>
                        
                    </div>
                    <div className={styles.bottomBox} onClick={addCart}>
                        <h4>{(data.price*quantity).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h4>
                    </div>                   
                </div>
            </>
        )
    }
   
}
export default MenuSelect;