import styles from 'css/admin/orderlist.module.scss'
import { useEffect } from 'react';
import { useState } from 'react';
import { getOrders } from 'web/axios/orderAPI';
function OrderList() {
    let [data, setData] = useState([]);
    useEffect(() => {
        getOrders().then((res) => {
            setData(res.data);
        })
    }, [])
    useEffect(()=>{
        console.log("data=",data);
    },[data])
    const cook_Start = (i)=>{
        let arr = [...data];
        arr[i].orderStatus = '';
        setData([...arr]);
    }
    const cook_End = (i)=>{
        let arr = [...data];
        arr[i].orderStatus = '';
        setData([...arr]);
    }
    return (
        <div className={styles.wrap}>
            <div className={styles.top}>
                <h4>  </h4>
                <hr></hr>
                <ul className={styles.pl_15}>
                    {data.map((ele,i) => {
                        if (ele.orderStatus === '') {
                            return (
                                <li key={ele.orderId}>
                                    <Card order={ele} clickEvent={()=>{cook_Start(i)}} btnmsg={">"}></Card>
                                </li>
                            )
                        }
                    })}
                </ul>
            </div>
            <div className={styles.bottom}>
                <h4> </h4>
                <hr></hr>
                <ul className={styles.pl_15}>
                    {data.map((ele,i) => {
                        if (ele.orderStatus === '') {
                            return (
                                <li key={ele.orderId}>
                                    <Card  order={ele} clickEvent={()=>{cook_End(i)}} btnmsg={">"}></Card>
                                </li>
                            )
                        }
                    })}
                </ul>
            </div>
        </div>
    )
}

function Card({ order, btnmsg ,clickEvent}) {

    return (
        <div className={styles.cardwrap}>
            <h5> <span style={{ color: 'red' }}>{order.orderId}</span> <span className={styles.time}>{order.orderTime.substring(10)}</span></h5>
            <div className={styles.tb}>
                <table>
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {order.orderItems.map((ele) => {
                            return (
                                <tr key={ele.itemCode}>
                                    <td>
                                        {ele.itemName}
                                    </td>
                                    <td>
                                        {ele.count}
                                    </td>
                                </tr>
                            )
                        })}
                    </tbody>
                </table>
            </div>

            <div>
                <button onClick={clickEvent}>{btnmsg}</button>
            </div>
        </div>
    )
}
export default OrderList;